#footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.content-wrapper {
  width: 100%;
  background-color: #fff;
  margin-top: 30px;
}

.content {
  max-width: 900px;
  padding: 30px;
  height: 100%;
  position: relative;
  margin: 0 auto;
  background-color: #fff;
}

.content h2 {
  margin-bottom: 30px;
}

.content h3 {
  margin-bottom: 15px;
}

#root, 
.page {
  height: 100%;
  min-height: 100%;
}

#login-button, #logout-button {
  width: 100px;
}

a:hover {
  cursor: pointer;
}
