#handelsbanken #header {
    padding: 15px;
    background-color: #fff;
    border-bottom: 2px solid #005fa5;
}

#handelsbanken #login-button,
#handelsbanken #logout-button,
#handelsbanken #back {
    border: none;
    background-color: #005fa5;
    height: 40px;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

#handelsbanken #login-button,
#handelsbanken #logout-button {
    float: right;
}

#handelsbanken #login-button,
#handelsbanken #logout-button,
#handelsbanken #back a {
    color: #fff;
    font-weight: 600;
}

#handelsbanken #back {
    min-width: 300px;
    max-width: 900px;
    margin: 0 auto;
    display: block;
}

#handelsbanken #login-button:hover, 
#handelsbanken #logout-button:hover, 
#handelsbanken #back:hover {
    background-color: #043b62;
}