#beijer #header {
    padding: 15px;
    background-color: #fff;
    border-bottom: 2px solid #000;
}

#beijer #login-button,
#beijer #logout-button,
#beijer #back {
    border: none;
    background-color: #fed141;
    height: 40px;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

#beijer #login-button,
#beijer #logout-button {
    float: right;
}

#beijer #login-button,
#beijer #logout-button,
#beijer #back a {
    color: #000;
    font-weight: 600;
}

#beijer #back {
    min-width: 200px;
    max-width: 900px;
    margin: 0 auto;
    display: block;
}

#beijer #login-button:hover, 
#beijer #logout-button:hover, 
#beijer #back:hover {
    background-color: #fcc101;
}