#roblox #header {
    padding: 15px;
    background-color: #fff;
    border-bottom: 2px solid #000;
}

#roblox #header #login-button,
#roblox #header #logout-button,
#roblox #footer #back {
    border: none;
    background-color: #fff;
    height: 40px;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

#roblox #header #login-button,
#roblox #header #logout-button {
    float: right;
}

#roblox #header #login-button,
#roblox #header #logout-button,
#roblox #footer #back a {
    color: #fff;
    font-weight: 600;
}

#roblox #footer #back {
    min-width: 300px;
    max-width: 900px;
    margin: 0 auto;
    display: block;
}

#roblox #header #login-button:hover, 
#roblox #header #logout-button:hover, 
#roblox #footer #back:hover {
    background-color: #8ee7e4;
}