#home #header {
    padding: 15px;
    border-bottom: 2px solid #048cb1;
    background: rgb(2,139,183);
    background: -moz-linear-gradient(180deg, rgba(2,139,183,1) 0%, rgba(1,162,170,1) 50%, rgba(1,189,143,1) 100%);
    background: -webkit-linear-gradient(180deg, rgba(2,139,183,1) 0%, rgba(1,162,170,1) 50%, rgba(1,189,143,1) 100%);
    background: linear-gradient(180deg, rgba(2,139,183,1) 0%, rgba(1,162,170,1) 50%, rgba(1,189,143,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#028bb7",endColorstr="#01bd8f",GradientType=1);
}

#home #header h1 {
    color: #fff;
    font-weight: 400;
}

#home ul {
    border: 1px solid #cdcdcd;
}

#home li {
    box-sizing: border-box;
    width: 100%;
    border-bottom: 1px solid #cdcdcd;
}

#home #nav {
    margin-top: 30px;
}

#home #nav a {
    display: block;
    box-sizing: border-box;
    width: 100%;
    padding: 15px;
    font-weight: 400;
    letter-spacing: 3px;
}

#home li:last-child {
    border-bottom: none;
}

#home li:hover {
    cursor: pointer;
    background-color: #f1f1f1;
}
