html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
  font-family: sans-serif;
  font-weight: 200;
	vertical-align: baseline;
  list-style-type: none;
  text-decoration: none;
  color: #000;
  cursor: default;
}
*:focus {
    outline:0;
}

textarea {
  resize: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body,
html    {
	line-height: 1;
    background-color: #f5f5f5;
    min-height: 100%;
    height:100%;
    position:relative;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
    border-collapse:separate;
    border-spacing:0;
    width:100%;
    height:100%;
    min-height:100%;
    position:relative;
}

input,
textarea,
select,
button {
    font-family: sans-serif;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="button"],
input[type="submit"],
textarea,
button {
  box-sizing: border-box;
}

a, 
button {
  letter-spacing: 2px;
}

#back {
  width: 100%;
}

#answers td {
  /*border-bottom: 1px solid #cdcdcd;
  border-left: 1px solid #cdcdcd;*/
  padding: 10px 5px;
}

/*#answers td:last-child {
  border-right: 1px solid #cdcdcd;
}*/

/*#answers tr:first-child td {
  border-top: 1px solid #cdcdcd;
}*/

#footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.content-wrapper {
  width: 100%;
  background-color: #fff;
  margin-top: 30px;
}

.content {
  max-width: 900px;
  padding: 30px;
  height: 100%;
  position: relative;
  margin: 0 auto;
  background-color: #fff;
}

.content h2 {
  margin-bottom: 30px;
}

.content h3 {
  margin-bottom: 15px;
}

#root, 
.page {
  height: 100%;
  min-height: 100%;
}

#login-button, #logout-button {
  width: 100px;
}

a:hover {
  cursor: pointer;
}

#home #header {
    padding: 15px;
    border-bottom: 2px solid #048cb1;
    background: rgb(2,139,183);
    background: linear-gradient(180deg, rgba(2,139,183,1) 0%, rgba(1,162,170,1) 50%, rgba(1,189,143,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#028bb7",endColorstr="#01bd8f",GradientType=1);
}

#home #header h1 {
    color: #fff;
    font-weight: 400;
}

#home ul {
    border: 1px solid #cdcdcd;
}

#home li {
    box-sizing: border-box;
    width: 100%;
    border-bottom: 1px solid #cdcdcd;
}

#home #nav {
    margin-top: 30px;
}

#home #nav a {
    display: block;
    box-sizing: border-box;
    width: 100%;
    padding: 15px;
    font-weight: 400;
    letter-spacing: 3px;
}

#home li:last-child {
    border-bottom: none;
}

#home li:hover {
    cursor: pointer;
    background-color: #f1f1f1;
}

.answer h4 {
    /*font-weight: 400;*/
    margin-bottom: 5px;
}

.answer {
    margin-top: 15px;
}

.answer input[type="text"] {
    font-size: 16px;
    padding: 7px;
    letter-spacing: 1.5px;
    width: 300px;
    color: #000;
    -webkit-text-fill-color: #000;
    opacity: 1; /* required on iOS */
}

#tickster #header {
    padding: 15px;
    background-color: #fff;
    border-bottom: 2px solid #1dcfc9;
}

#tickster #header #login-button,
#tickster #header #logout-button,
#tickster #footer #back {
    border: none;
    background-color: #1dcfc9;
    height: 40px;
    padding: 0;
    margin: 0;
    cursor: pointer;
}

#tickster #header #login-button,
#tickster #header #logout-button {
    float: right;
}

#tickster #header #login-button,
#tickster #header #logout-button,
#tickster #footer #back a {
    color: #fff;
    font-weight: 600;
}

#tickster #footer #back {
    min-width: 300px;
    max-width: 900px;
    margin: 0 auto;
    display: block;
}

#tickster #header #login-button:hover, 
#tickster #header #logout-button:hover, 
#tickster #footer #back:hover {
    background-color: #8ee7e4;
}
